@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
.login-page {
  background: #0d0d17;
  height: 100vh;
  width: 100%;
}

.header-login {
  padding: 1em;
}

.logo-yourwallet {
  font-size: 1.5em;
  font-weight: 600;
}

.container-form-login {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: flex-start;
  height: 90vh;
  width: 100%;
}

.form-login {
  display: flex;
  margin: 9em 0 0 0;
  flex-flow: column nowrap;
  width: 25%;
}

.login-to-your-account {
  font-weight: 500;
  font-size: 2em;
}

.input-login {
  background: #2c3838;
  border: 3px solid #2c3838;
  border-radius: 0.15em;
  transition: 0.2s ease-out all;
  outline: none;
  margin: 0.2em 0 1em 0;
  padding: 0.3em;
  color: #f2f2f2;
  font-size: 1em;
  height: 2.6em;
  width: 100%;
}

.input-login:focus {
  border: 3px solid #e6feee;
}

.btn-submit {
  border-radius: 0.2em;
  background: #b5fdcd;
  cursor: pointer;
  border: none;
  font-weight: 500;
  text-transform: uppercase;
  transition: 0.1s ease-out all;
  margin: 0.5em 0 0 0;
  height: 2.7em;
  width: 100%;
}

.btn-submit:hover {
  background: #83fbab;
}

.btn-submit:disabled {
  background: #e6feee;
}

.btn-submit:disabled:hover {
  cursor: auto;
  background: #e6feee;
}

.wallet-page {
  background: #0d0d17;
  height: 100vh;
  width: 100%;
}

.table,
tr,
td,
th {
  background: #39515B;
  border: 2px solid #4DB9C6;
  border-collapse: collapse;
  text-align: center;
  padding: 0.5em;
}

.table {
  margin: 1em 0.2em;
  width: 100%;
}

.tr-table td {
  padding: 1em 0.5em;
}

.buttons-update-delete {
  border: none;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
}

.btn-update-delete {
  align-items: center;
  border: none;
  border-radius: 0.2em;
  cursor: pointer;
  display: flex;
  flex-flow: row nowrap;
  height: 2em;
  justify-content: center;
  outline: none;
  width: 40%;
}

.btn-update {
  background: #f9f91a;
}

.btn-update:hover {
  background: #e0e006;
}

.btn-delete {
  background: #f91a1a;
}

.btn-delete:hover {
  background: #e00606;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #0d0d17;
  color: #f2f2f2;
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
  height: 99vh;
  padding: 1em;
  width: 99vw;
}

